import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { NplistPage } from '../nplist/nplist';
import { ItineraryPage } from '../itinerary/itinerary';
import { Storage } from '@ionic/storage';
import { Toast } from '@ionic-native/toast';

/**
 * Generated class for the CustomPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-custom',
  templateUrl: 'custom.html',
})
export class CustomPage {

  itinerary= [];
  constructor(public navCtrl: NavController, public navParams: NavParams, public storage: Storage, private toast: Toast) {
    this.storage.keys().then(data => {
      this.itinerary = data;
    });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad CustomPage');
  }

  toNationalParkList(){
    this.navCtrl.push(NplistPage);
  }

  toShoppingCart(){
    if(this.itinerary.length === 0){
      this.toast.show(`No Items Added To Itinerary`, '5000', 'center').subscribe(
        toast => {
          console.log(toast);
        });
      }
    this.navCtrl.push(ItineraryPage);
  }
}



